.header {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    gap: 16px;

    &__page {
        font-size: 20px;
        line-height: 1.4;
        cursor: pointer;
    }
}