.map-object-list {
    display: flex;
    flex-direction: column;
    width: 35%;
    min-width: 640px;
    height: 640px;
    overflow-y: auto;
    overflow-x: hidden;

    &__list {
        display: flex;
        flex-direction: column;
        overflow-y: scroll;
    }

    &__header {
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        width: 100%;
        padding: 12px 0px 16px 32px;
        background-color: #DFE6EB;
    }
}